<template>
	<div class="page-dial">
		<div class="post-title"><span class="name">啦啦啦</span>获得30元优惠券</div>
		<div class="dial">
			<img :src="this.baseUrl+'rotary_ic_rotary.png'" alt="">
		</div>
		<div class="chance text-center">您有1次抽奖机会</div>
		<div class="buttons row">
			<div class="col-12"><span class="btn">活动规则</span></div>
			<div class="col-12"><span class="btn right">我的奖品</span></div>
		</div>
		<div class="footer text-center">
			<span class="btn footer">
				前往主会场
				<img :src="this.baseUrl+'rotary_ic_arrow.png'" alt="">
			</span>
		</div>
	</div>
</template>
<script>
export default {
	name: 'dial',
	data () {
		return {
			baseUrl: this.cdn + '171111/'
		}
	}
}
</script>

<style lang="scss">
@import 'src/assets/css/common.scss';
.page-dial{
	height: 100%;
	background-image: url(#{$url-171111}rotary_ic_back_02.png);
	background-size: 100% 100%;
	padding-top: 2.2rem;
	.post-title{
		font-size: .26rem;
		padding: .09rem 0;
		color: #fff;
		text-align: center;
		background-image: url(#{$url-171111}rotary_ic_rectangle.png);
		.name{color: #ffd779;}
	}
	.dial{
		margin-top: .4rem;
		img{width: 7.14rem;}
	}
	.chance{color: #e75b4c;font-size: .26rem;margin-top: .1rem;}
	.buttons{
		margin-top: .16rem;
		text-align: center;
	}
	.footer{margin-top: .5rem;}
	.btn{
		font-size: .34rem;
		color: #fff;
		width: 3.1rem;
		height: .83rem;
		line-height: .83rem;
		display:inline-block;
		background-image: url(#{$url-171111}button_ic_rule.png);
		&.right{
			background-image: url(#{$url-171111}button_ic_prizes.png)
		}
		&.footer{
			height: .7rem;
			width: 2.64rem;
			line-height: .7rem;
			font-size: .3rem;
			position: relative;
			background-image: url(#{$url-171111}button_ic_main.png)
		}
	}
}
</style>



